<template>
  <div class="about">
    <h1>글을 작성하는 공간입니다</h1>
    <input type="text" v-model="title" size="28"> <br>
    <textarea v-model="memo" cols="30" rows="10">
    </textarea><br>
    <button @click="addmemoMu">완료</button>
  </div>
</template>

<script>
export default {
  data : function () {
    return {
      title : '',
      memo : '',
    }
  },
  methods : {
    addmemoMu : function () {
      const memo = { title : this.title, memo: this.memo }
      this.$store.commit('addmemo', memo);
      this.$router.push('/');
    }
  }
}
</script>